@import 'components/variables';

.btn-rounded {
  border-radius: 10px;
  line-height: 1.75;
}
.btn-custom {
  font-size: .6rem;
  @media (min-width: 450px) {
    font-size: .8rem;
  }
}
.btn-white-transparent {
  padding-left: 1rem;
  padding-right: 1rem;
  color: white;
  font-weight: bold;
  background: transparent;
  transition: all 0.3s;
  border: 1px solid white;
  &:hover {
    color: #B787B1;
  }
  &:before {
    background: white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: #B787B1;
  }
}
.btn-yellowgr-blue {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  font-size: 1.2rem;
  font-weight: bold;
  background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
  border: none;
  transition: all 0.3s;
  a {
    font-size: 1rem;
  }
  &:hover {
    color: $purple;
    box-shadow: 0 0 1px 1px $yellow;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $purple;
  }
}
.btn-yellowgr-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  font-weight: bold;
  background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $blue;
    box-shadow: 0 0 1px 1px $yellow;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $blue;
  }
}
.btn-white-purple {
  padding-left: 3rem;
  padding-right: 3rem;
  background: transparent;
  border: 2px solid #6E60E7;
  color: #6E60E7;
  font-weight: 700;
  transition: color 0.3s, background 0.5s;
  &:hover {
    color: $white;
    background: #6E60E7;
  }
  &:before {
    background: #6E60E7;
    color: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $white;
  }
}
.btn-purple-white {
  padding-left: 3rem;
  padding-right: 3rem;
  background: #6E60E7;
  transition: all 0.3s;
  border: 2px solid #6E60E7;
  color: $white;
  font-weight: 700;
  &:hover {
    color: #6E60E7;
  }
  &:before {
    background: $white;
    color: #6E60E7;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: #6E60E7;
  }
}
.btn-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
}
.btn-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s;
}
.btn-center-out:hover:before,
.btn-center-out:focus:before,
.btn-center-out:active:before {
  transform: scale(1.15);
}