@import 'components/variables';
@import 'components/buttons';

.section-producto {
  padding-top: 10%;
  padding-bottom: 5%;
  .title-section {
    font-size: 1.8rem;
    font-weight: bold;
    color: #2C53A2;
    margin-bottom: 2rem;
  }
  .slider-productos {
    position: relative;
    width: 85%;
    margin: 0 auto;
    .btn-categoria {
      border-radius: 50%;
      background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 2rem;    
      left: 0;
      transition: all .3s;
      z-index: 1;
      @media (min-width: 600px) {
        transform: translateX(-50%);
      }
      .categoria {
        color: white;
        margin-bottom: 0;
        font-weight: 400;
        font-size: 1rem;
        @media (min-width: 425px) {
          font-size: .8rem;
        }
      }
    }
    .slider-for {
      background-color: #EDEDED;
      border-radius: 10px;
      margin-bottom: 1rem;
      .slick-next {
        width: 10%;
        right: -25px;
        z-index: 1;
        @media (min-width: 425px) {
          right: -35px;
        }
        @media (min-width: 600px) {
          right: -45px;
        }
        @media (min-width: 768px) {
          right: -60px;
        }
        @media (min-width: 992px) {
          right: -40px;
        }
        @media (min-width: 1200px) {
          right: -50px;
        }
        &:before {
          content: url('../images/arrow-right-p.svg');
        }
      }
      .slick-prev {
        width: 10%;
        z-index: 1;
        left: -25px;
        @media (min-width: 425px) {
          left: -35px;
        }
        @media (min-width: 600px) {
          left: -45px;
        }
        @media (min-width: 768px) {
          left: -60px;
        }
        @media (min-width: 992px) {
          left: -40px;
        }
        @media (min-width: 1200px) {
          left: -50px;
        }
        &:before {
          content: url('../images/arrow-left-p.svg');
        }
      }
    }
    .slider-nav {
      .slick-slide {
        background-color: #EDEDED;
        margin: 0 .5rem;
      }
      .slick-next {
        width: 5%;
        right: -25px;
        z-index: 1;
        &:before {
          content: url('../images/right.svg');
        }
      }
      .slick-prev {
        width: 5%;
        z-index: 1;
        left: -25px;
        &:before {
          content: url('../images/left.svg');
        }
      }
    }
     
  }
  .compartir-redes {
    background-color: #ededed;
    border-radius: 50px;
    display: flex;
    align-items: center;
    padding: .5rem 1rem;
    ul {
      display: flex;
      align-items: center;
      list-style: none;
      margin-bottom: 0;
      li {
        transition: all .3s;
        padding: 0 1rem;
        cursor: pointer;
        &:hover {
          transform: scale(0.8);
        }
      }
    }
  }
  .content-text {
    margin-top: 2rem;
    list-style: none;
    padding-left: 0;
  }
  .now-price {
    color: #3C3664;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 0;
  }
  .before-price {
    color: #BF3082;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 0;
    text-decoration: line-through;
    span {
      color: #3C3664;
    }
  }
  .col-derecha {
    display: flex;
    justify-content: center;
    align-items: center;
    .btn-yellowgr-blue {
      padding-left: 1rem;
      padding-right: 1rem;
      font-size: 1rem;
      @media (min-width: 1200px) {
        padding-left: 2rem;
        padding-right: 2rem;
        font-size: 1.2rem;
      }
    }
    a {
      .carrito {
        display: inline;
      }
      .carrito-purple {
        display: none;
      }
      &:hover {
        .carrito {
          display: none;
          transition: all .3s;
        }
        .carrito-purple {
          display: inline;
          transition: all .3s;
        }

      }
    }
  }
}

.section-caracteristicas {
  .tabs {
    background: radial-gradient(circle, rgba(45, 54, 131, 1) 0%, rgba(5, 23, 35, 1) 70%);
    padding: 0;
    list-style: none;
    position: relative;
    padding: 2rem 0;
    font-size: .8rem;
    @media (min-width: 485px) {
      font-size: 1rem;
    }
    @media (min-width: 1200px) {
      font-size: 1.2rem;
      font-weight: bold;
    }
    .active-tab {
      position: relative;
      color: #FEAC0B;
      border-bottom: none;
    }
    li {
      display: inline-block;
      cursor: pointer;
      color: #fff;
      padding: .5rem 1rem;
      @media (min-width: 768px) {
        padding: .5rem 2rem;
      }
      &:nth-child(2) {
        border-left: 1px solid #ffffffaa;
        border-right: 1px solid #ffffffaa;
      }
    }
  }
  .tabs-content {
    margin-top: 3rem;
    padding: 0;
    list-style: none;
    li {
      background-color: #EDEDED;
      padding: 3rem;
      border-radius: 10px;
      display: none;
      position: relative;
      .triangulo1,
      .triangulo2,
      .triangulo3 {
        position: absolute;
        top: -40px;
        left: 0;
        width: 0;
        height: 0;
        border-right: 20px solid transparent;
        border-top: 20px solid transparent;
        border-left: 20px solid transparent;
        border-bottom: 20px solid #EDEDED;
      }
      .triangulo1 {
        transform: translateX(25px);
        @media (min-width: 360px) {
          transform: translateX(42px);
        }
        @media (min-width: 425px) {
          transform: translateX(50px);
        }
        @media (min-width: 485px) {
          transform: translateX(60px);
        }
        @media (min-width: 768px) {
          transform: translateX(80px);
        }
      }
      .triangulo2 {
        transform: translateX(100px);
        @media (min-width: 360px) {
          transform: translateX(135px);
        }
        @media (min-width: 425px) {
          transform: translateX(170px);
        }
        @media (min-width: 485px) {
          transform: translateX(210px);
        }
        @media (min-width: 768px) {
          transform: translateX(250px);
        }
      }
      .triangulo3 {
        transform: translateX(180px);
        @media (min-width: 360px) {
          transform: translateX(225px);
        }
        @media (min-width: 425px) {
          transform: translateX(290px);
        }
        @media (min-width: 485px) {
          transform: translateX(345px);
        }
        @media (min-width: 768px) {
          transform: translateX(430px);
        }
      }
    }
  }
}
